import React, {PureComponent} from 'react'
import {
  Modal,
  Row,
  Col,
  Table,
  Tabs
} from 'antd'
import {connect} from 'react-redux'
import {mapDispatchToProps} from '../redux/mapDispatchToProps'
const TabPane = Tabs.TabPane

@connect(
  state => ({...state.packageList, userLoation: state.userLoation}),
  mapDispatchToProps
)
export default class app extends PureComponent {
  constructor() {
    super()
    this.state = {
    }
    this.columns = [{
      title: '商品编号',
      width: 150,
      dataIndex: 'productCode',
      key: 'productCode'
    }, {
      title: '商品名称',
      width: 150,
      dataIndex: 'productName',
      key: 'productName'
    }, {
      title: '单位',
      width: 100,
      dataIndex: 'unit',
      key: 'unit'
    }, {
      title: '打包数量',
      width: 100,
      dataIndex: 'qty',
      key: 'qty'
    }]
  }
  componentDidMount() {
    this._getAsyncData()
  }
  // 根据tabs的key来判断请求哪个接口
  _getAsyncData() {
    const {handleGetDetailPrint, match: {params}} = this.props
    handleGetDetailPrint({searchParams: {ids: [params.id]}}, 2)
  }
  // 关闭弹窗
  handleCloseModal = () => {
    const {history} = this.props
    history.length > 1 ? history.goBack() : window.close()
  }
  render() {
    const {deliveryTableLoading, printData} = this.props
    let modalData = printData[0] || {}
    return (
        <div className='fullOrderDel'> 
          <span className='ant-modal-close-x close' onClick={this.handleCloseModal}></span>
          <div className='content'>
              <div className='header'>
                <Row type="flex" justify="start">
                  <Col span={8} className='delBox'>
                      <label>包裹号：</label>
                      <span>{modalData.header && modalData.header.orderCode}</span>
                  </Col>
                  <Col span={8} className='delBox'>
                      <label>波次号：</label>
                      <span>{modalData.header && modalData.header.waveCode}</span>
                  </Col>
                  <Col span={8} className='delBox'>
                      <label>出库号：</label>
                      <span>{modalData.header && modalData.header.outCode}</span>
                  </Col>
                  <Col span={8} className='delBox'>
                    <label>状态：</label>
                    <span>{modalData.header && modalData.header.statusCn}</span>
                </Col>
                  <Col span={8} className='delBox'>
                    <label>创建时间：</label>
                    <span>{modalData.header && modalData.header.createTime}</span>
                  </Col>
                  <Col span={8} className='delBox'>
                    <label>出库时间：</label>
                    <span>{modalData.header && modalData.header.sendTime}</span>
                  </Col>
                </Row>
              </div>
              <div className='foot'>
                <div className='table-group' style={{margin: 0}}>
                  <Table
                    rowKey={record => record.id}
                    loading={deliveryTableLoading}
                    columns={this.columns}
                    scroll={{x: 500, y: 400}}
                    dataSource={modalData.items}
                    pagination={false}
                  />
                </div>
              </div>
          </div>
        </div>
    )
  }
}



