import * as actionType from './actionType'
import {getData} from 'util'
import {API_PACKAGES_PAGE, API_PACKAGES_CLOSE, API_PRODUCTOUT_OUT, API_PRINTPACKAGEINFO_PRINT, API_PRINTOUTINFO_PRINT, API_UPDPRINTTIMES, API_SHIPPEDORDERPRINTTIMES, API_PACKAGELIST_LOADCOUNTS, API_PACKAGE_APPLY_SHIPPEDORDERCODE, API_PACKAGE_ONEKEY, API_PACKAGE_COMPLETE, API_PACKAGE_PRINT_BATCH} from 'api'
//同步
export const update = (object) => ({
  type: actionType.PACKAGELIST_UPDATE,
  data: object
})

//异步

// 获取列表
export const handleGetList = (reqData, status) => (dispatch) => {
  dispatch({type: actionType.PACKAGELIST_UPDATE, data: {tableLoading: true}})
  let reqObjSearchParams = {...reqData.searchParams, type: 'N'}
  for (let i in reqObjSearchParams) {
    if (Object.prototype.toString.call(reqObjSearchParams[i]) === '[object Object]') {
      reqObjSearchParams[i] = reqObjSearchParams[i].value
    }
  }
  reqData.searchParams = reqObjSearchParams

  if (status) {
    getData({api: API_PACKAGES_PAGE, ...reqData}).then(data => {
      // 相应的逻辑判断
      let {page, size} = reqData.searchParams
      dispatch({
        type: actionType.PACKAGELIST_UPDATE,
        data: {
          page,
          size,
          tableLoading: false,
          total: data.result.total,
          selectedRowKeys: [], // 选中的队列key
          selectedRows: [], // 选中的队列数据
          searchResult: data.result.list || []
        }
      })
    }).catch(err => {
      dispatch({
        type: actionType.PACKAGELIST_FILE
      })
    })
  } else {
    // 查询统计数量tabs
    getData({api: API_PACKAGELIST_LOADCOUNTS, ...reqData}).then(({result}) => {
      // 相应的逻辑判断
      let arr = ['0', '1', '2', '3', '4']
      let status = ''
      let tabNumObj = {
        tab0: '',
        tab1: '',
        tab2: '',
        tab3: '',
        tab4: ''
      }
      result.map((o) => {
        tabNumObj[`tab${o.key}`] = o.value
      })
      for (let i = 0; i < arr.length; i++) {
        for (let j = 0; j < result.length; j++) {
          if (result[j].key == arr[i] && result[j].value) {
            status = arr[i]
            break
          }
        }
        if (status) break
      }
      if (status) {
        reqData.searchParams.status = status
      }
      getData({api: API_PACKAGES_PAGE, ...reqData}).then(data => {
        // 相应的逻辑判断
        let {page, size} = reqData.searchParams
        dispatch({
          type: actionType.PACKAGELIST_UPDATE,
          data: {
            page,
            size,
            tableLoading: false,
            total: data.result.total,
            tableActiveKey: status,
            selectedRowKeys: [], // 选中的队列key
            selectedRows: [], // 选中的队列数据
            searchResult: data.result.list || [],
            ...tabNumObj
          }
        })
      }).catch(err => {
        dispatch({
          type: actionType.PACKAGELIST_FILE
        })
      })
    }).catch(err => {
      dispatch({
        type: actionType.PACKAGELIST_FILE
      })
    })
  }
}

// 1单批量打印面单 && 2打印按包裹打印出库
export const handleGetDetailPrint = (reqData, type) => (dispatch) => {
  dispatch({type: actionType.PACKAGELIST_UPDATE, data: {printLoading: true}})
  let api = type == 1 ? API_PRINTPACKAGEINFO_PRINT : API_PRINTOUTINFO_PRINT
  getData({api, ...reqData}).then(data => {
    // 相应的逻辑判断
    dispatch({
      type: actionType.PACKAGELIST_UPDATE,
      data: {
        printLoading: false,
        printData: data.result || []
      }
    })
  }).catch(err => {
    dispatch({
      type: actionType.PACKAGELIST_FILE
    })
  })
}

// 1出库 && 2取消
export const handleClose = (reqData, type, callBack) => (dispatch) => {
  let api = type == 1 ? API_PRODUCTOUT_OUT : API_PACKAGES_CLOSE
  getData({api, ...reqData}).then(data => {
    // 相应的逻辑判断
    dispatch({
      type: actionType.PACKAGELIST_UPDATE,
      data: {
        tableLoading: false,
        selectedRowKeys: [], // 选中的队列key
        selectedRows: [] // 选中的队列数据
      }
    })
    callBack && callBack(data)
  }).catch(err => {
    dispatch({
      type: actionType.PACKAGELIST_FILE
    })
  })
}

// 一键打包, 完成打包
export const handlePackage = (reqData, type, callBack) => (dispatch) => {
  let api = API_PACKAGE_ONEKEY
  if (type == 2) {
    api = API_PACKAGE_COMPLETE
  }
  getData({api, ...reqData}).then(data => {
    // 相应的逻辑判断
    dispatch({
      type: actionType.PACKAGELIST_UPDATE,
      data: {
        tableLoading: false,
        selectedRowKeys: [], // 选中的队列key
        selectedRows: [] // 选中的队列数据
      }
    })
    callBack && callBack()
  }).catch(err => {
    dispatch({
      type: actionType.PACKAGELIST_FILE
    })
  })
}

// 更新打印次数
export const handleUpdatePrintNum = (reqData, type, callBack) => (dispatch) => {
  let api = API_UPDPRINTTIMES
  if (type == 2) {
    api = API_SHIPPEDORDERPRINTTIMES
  }
  getData({api, ...reqData}).then(data => {
    // 相应的逻辑判断
    callBack && callBack()
  }).catch(err => {
    dispatch({
      type: actionType.PACKAGELIST_FILE
    })
  })
}

//申请新包裹单
export const handleNewPackage = (reqData, callBack) => (dispatch) => {
  getData({api: API_PACKAGE_APPLY_SHIPPEDORDERCODE, ...reqData}).then(data => {
    callBack && callBack()
  }).catch(err => {
    dispatch({
      type: actionType.PACKAGELIST_FILE
    })
  })
}


//包裹列表判断模板
export const handlePackageJudge = (reqData, callBack) => (dispatch) => {
  getData({api: API_PACKAGE_PRINT_BATCH, ...reqData}).then(data => {
    callBack && callBack(data)
  }).catch(err => {
    dispatch({
      type: actionType.PACKAGELIST_FILE
    })
  })
}



